	module.exports = {"data":{"markdownRemark":{"html":"<p>Запуск первой программы на swi-prolog\n[cut]\n_ Алгоритм действий для gprolog/swi-prolog аналогичен:</p>\n<ul>\n<li>создать файл , например db.pl</li>\n<li>запустить терминал в той-же папке, что и файл</li>\n<li>запустить gprolog/swipl</li>\n<li>ввести [db.pl]. или consult('db.pl'). точка на конце обязательна, как и во всех запросах prolog, что вводятся через консоль(вопросы к программе)</li>\n</ul>\n<p>_ Процесс описан для Linux, однако он мало чем отличается на других ОС.\n_ ссылки:</p>\n<ul>\n<li>Подробнее про загрузку и запуск программ в главе 3.6 стр 54 книги PROLOG – ЯЗЫК РАЗРАБОТКИ ИНТЕЛЛЕКТУАЛЬНЫХ И ЭКСПЕРТНЫХ СИСТЕМ автор С.П.Хабаров, есть в свободном доступе в интернете.\n_ Код программ можно подсмотреть тут , код программы можно скопировать из [url=http://stud.ashcherbakov.ru/page/razlichija-turboprolog-i-gnuswi-prolog]Различия TurboProlog и GNU/SWI Prolog[/url], вконце статьи есть лабораторные работы и решения к ним в виде готовых программ\n_ Хорошее видео - изучение prolog </li>\n<li><a href=\"https://www.youtube.com/watch?v=SykxWpFwMGs\">Prolog Tutorial by Derek Banas</a>, видео с временными метками, по каждому разделу, под видео ссылка на текст видео(кратенько и по делу, особенно радуют куски кода, что-бы не набирать ручками)</li>\n</ul>","frontmatter":{"date":"2016-05-19","path":"/page/swi-prolog-kak-sozdat-i-zapustit-pervuju-programmu-linux","title":"Swi-prolog как создать и запустить первую программу Linux"}}},"pathContext":{}}

